import { GET_SHOPS } from "../types";

export const shopsReducer = (state, action) => {
    switch (action.type) {
        case GET_SHOPS:
            return {
                ...state,
                users: action.payload,
                loading: false,
            }
           
        // case GET_SHOP:
        //     return state
        default:
            return state
    }
}