import React, {useReducer} from "react";
import { GET_SHOPS } from '../types'
import { shopsReducer } from "./shopsReducer";
import { shopsContext } from "./shopsConext";
import axios from "axios";

const ShopsState = ({children}) => {
    const initialState= {
        loading: false,
        shops: []
    }

    const [state, dispatch] = useReducer(shopsReducer, initialState)
    const renderShops = async () => {
        const response = await axios.get('https://map-with-navigation-default-rtdb.firebaseio.com/shops.json')

        dispatch({
            type: GET_SHOPS,
            payload: response.data.shops
        })
    }
     


    return (
        <shopsContext.Provider value={{renderShops}}>
            {children}
        </shopsContext.Provider>
    )
}

export default ShopsState